@font-face {
  font-family: 'F2';
  src: url('./mario_kart_f2.ttf') format('truetype');
}

.Header {
  grid-area: header;
  width: 100%;
  height: 100%;
  padding: 1rem;
  box-sizing: border-box;
  background: linear-gradient(#26baff, #f92470);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.25);
  line-height: 4rem;
}

.Logo {
  height: 100%;
  width: 31rem;
  margin: auto;
}

.LogoImg {
  float: left;
  height: 100%;
  margin-right: 1rem;
}

.LogoText {
  color: white;
  font-size: 36px;
}
