@import url(https://fonts.googleapis.com/css2?family=Prompt&display=swap);
*{border:0;margin:0;padding:0}body{font-family:'Prompt', Arial, sans-serif;width:100vw;min-width:80rem;height:100vh;background:#333333;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

@font-face{font-family:'F2';src:url(/kart-optimizer/static/media/mario_kart_f2.0651abbb.ttf) format("truetype")}.Header{grid-area:header;width:100%;height:100%;padding:1rem;box-sizing:border-box;background:linear-gradient(#26baff, #f92470);box-shadow:0 5px 10px rgba(0,0,0,0.25);line-height:4rem}.Logo{height:100%;width:31rem;margin:auto}.LogoImg{float:left;height:100%;margin-right:1rem}.LogoText{color:white;font-size:36px}

.KartOptimizerApp{display:grid;grid-template-areas:'header header' 'kart-config sidebar';grid-template-rows:6rem calc(100vh - 6rem);grid-template-columns:calc(100vw - 34rem) 34rem}

.KartConfig{grid-area:kart-config;display:flex;justify-content:center;align-items:center;box-sizing:border-box;width:100%;height:100%;padding:2rem;background:#222}.CenterContainer{width:40rem;height:44.25rem}.PartOptions{display:flex}.Separator{width:calc(100% - 1rem);height:0.25rem;background:linear-gradient(to right, #26baff, #f92470);border-radius:1rem;margin:1rem 0.5rem}.NoBackend{font-size:24px;text-align:center;color:white;line-height:2}.NoBackend span{background:gray;padding:0.5rem;border-radius:0.5rem;white-space:nowrap;font-size:20px;font-family:'Courier New', Courier, monospace}.NoBackend a{text-decoration:none;color:#26baff}

.PartSelection{box-sizing:border-box;width:9em;height:12.25rem;margin:0.5rem}.PartLock{margin-top:1rem;padding:0.5rem;border-radius:2rem;transition:0.2s}.PartLock:hover{-webkit-filter:brightness(70%);filter:brightness(70%)}.PartLabel{color:white;box-sizing:border-box;font-size:18px;text-align:center;white-space:nowrap}.SelectedPart{display:flex;align-items:center;justify-content:center;box-sizing:border-box;width:9rem;height:9rem;padding:0.5rem;border-radius:2rem;transition:0.2s}.SelectedPart:hover{-webkit-filter:brightness(70%);filter:brightness(70%)}

.PartTile{position:relative;display:flex;justify-content:center;box-sizing:border-box;align-items:center;box-sizing:border-box;width:4rem;height:4rem;margin:0.5rem;padding:0.25rem;border-radius:1rem;transition:0.2s}.PartTile:hover{-webkit-filter:brightness(70%);filter:brightness(70%)}.PartImage{width:100%}

.PartList{display:flex;flex-wrap:wrap;justify-content:center}

.ProgressBar{width:100%;color:white;font-size:20px;margin-bottom:1rem}.ProgressBar p{margin-bottom:0.25rem}.Bar{position:relative;width:100%;background:#777;border-radius:1rem;height:1rem;background:linear-gradient(to right, #26baff, #f92470);overflow:hidden}.SeparatorContainer{position:absolute;display:flex;justify-content:space-evenly;align-items:center;width:100%;height:1rem;top:0}.Progress{position:absolute;right:0;top:0;height:1rem;background:gray}.VerticalSeparator{height:100%;width:2px;background:#333}

.PartStats{grid-area:kart-stats;box-sizing:border-box;width:100%;height:24rem;padding:2rem}

.KartCombo{width:100%;height:8rem;border-radius:1rem;background:#777;overflow:hidden;transition:0.2s;margin-bottom:1rem}.KartCombo:hover{-webkit-filter:brightness(70%);filter:brightness(70%)}.Parts{box-sizing:border-box;padding:1rem;float:right;width:calc(100% - 6rem);height:100%;display:flex;justify-content:space-evenly;align-items:center;flex-wrap:wrap}.Parts img{flex:1 1 50%;width:2.9rem}.Stats{box-sizing:border-box;padding:1rem;float:left;width:6rem;height:100%;line-height:1.2rem;background:#222;color:white}

.Optimizer{width:100%;background:#444;box-sizing:border-box;padding:0 2rem;height:26.452rem}.Options{height:100%;width:calc(100% - 12rem);box-sizing:border-box;overflow-y:scroll;padding:1rem;background:#333;border-radius:2rem}

.StatPriority{float:left;width:11rem;height:100%;margin-right:1rem}.StatPriority button{color:white;border-radius:1.5rem;font-size:20px;text-align:center;background:#777;font-family:'Prompt', Arial, sans-serif;background:linear-gradient(45deg, #26baff, #f92470);transition:0.2s;margin-top:1rem;padding:0.5rem;width:100%}.StatPriority button:hover{-webkit-filter:brightness(70%);filter:brightness(70%)}.StatList{box-sizing:border-box;width:100%;background:#333;padding:1rem;border-radius:2rem}.Stat{color:white;border-radius:1.5rem;padding:0.5rem;font-size:20px;text-align:center;margin:1rem 0;background:#777}.Stat:hover{-webkit-filter:brightness(90%);filter:brightness(90%)}.Stat:first-child{margin-top:0}.Stat:last-child{margin-bottom:0}

.Sidebar{grid-area:sidebar;box-sizing:border-box;width:100%;height:100%;background:#444;overflow-y:scroll}

